import { FakeChatWindow } from "@/components";
import Icon from "@/components/elements/Icon";
import { useState } from "react";
import styles from "./Landing.module.scss";

const Landing: React.FC = () => {
  const [request, setRequest] = useState("Hi, what this widget is?");
  const [response, setResponse] = useState(
    "Hello! I am a ai powered chat bot assistant, that can help you to get concise and structored info based on your custom content! Shall we start?)"
  );
  const [styleChanged, setStyleChanged] = useState(false);

  const onSetupTileClick = () => {
    setRequest('What you mean by "simple"?');
    setResponse(`
    - registering
    - creating project
    - ingesting your knowledge base
    - copy-pasting the generated script from your cabinet here to your web page
    - you're good to go!`);
  };

  const onCustomizationTileClick = () => {
    setRequest("What I can customize?");
    setResponse(
      "Both appearance and functionality are customizable. You can adjust styles of the widget as it fits to your site design, and add any additional info to the response that is bein generated by content_insight, for example - add some additional links from other customer support APIs."
    );
    setStyleChanged(true);
  };

  const onCostTileClick = () => {
    setRequest("What about that billing scheme?");
    setResponse(
      "Experience effortless payments with our Comprehensive Billing Scheme! Unlock all features for just $20/month, no hidden fees. Simple, transparent, and stress-free â€“ embrace the power of hassle-free subscriptions now!"
    );
  };

  return (
    <div className={"page"}>
      <div className={"pageContent " + styles.hero}>
        <section className={styles.contentBlock}>
          <div className={styles.textContentWrapper}>
            {" "}
            <div className={styles.header}>
              <h1>Chat with your custom content</h1>{" "}
              <p className={styles.subHeader}>
                AI powered chatbot for your custom knowledge base{" "}
              </p>
            </div>
            <ul className={styles.tilesWrapper}>
              <li onClick={onSetupTileClick}>
                <div className={styles.tileIcon}>
                  <Icon iconName="icon_feather" side={130} />
                </div>
                <p className={styles.tileHeader}>Simple to setup</p>
                <p className={styles.tileDescription}>
                  Intuitive and fast process of your custom AI chat bot setup.
                </p>
              </li>

              <li onClick={onCustomizationTileClick}>
                <div className={styles.tileIcon}>
                  <Icon iconName="icon_settings" side={130} />
                </div>
                <p className={styles.tileHeader}>Customizable</p>
                <p className={styles.tileDescription}>
                  Adjust the chatbot looks and functionality to your website
                  needs.
                </p>
              </li>

              <li onClick={onCostTileClick}>
                <div className={styles.tileIcon}>
                  <Icon iconName="icon_bill" side={130} />
                </div>
                <p className={styles.tileHeader}>Fixed cost</p>
                <p className={styles.tileDescription}>
                  Comprehensive billing scheme with fixed monthly subscriptionn
                  price.
                </p>
              </li>
            </ul>
          </div>

          <FakeChatWindow request={request} response={response} styleChanged={styleChanged} />
        </section>
      </div>
    </div>
  );
};

export default Landing;
